.container {
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	border: 2px solid #ebae12;
	border-radius: 36px;
	box-shadow: inset 0 0 #ebae12;
	transition: all 1s ease-out;
	&:hover {
		box-shadow: inset 22em 0 #ebae12;
	}
}

.title {
	font-size: 1rem;
	font-weight: bold;
}
